.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 100%;
    overflow: hidden;

    grid-template-areas: "header header " "nav content";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: content;
  background-color: #70E0FF;

}


